<template>
	<view class="">
		<view class=""  :style="' line-height: '+navHeightLine+'px'">
				<!-- 类型选择 -->
				<view class="width-000">
					<view class="position-relative width-000">
						<view style="box-sizing: border-box;" class=" seckill-title  px-3  d-flex a-center width-000">
							<view @tap="typeOpt(0)"   class="d-flex a-center"  style="width: 50%;">
								<view class="color-fff leftTitle"  style="font-size: 37rpx;">
									武汉市全城
								</view>
								<view :class="currentType == 0?'iconshangjiantou':'iconxia'" class="iconfont  color-fff font-27 ml-2" style="opacity: 0.6;">
									
								</view>
							</view>
						
						
						</view>
						<view class="position-absolute width-000 z-index-1 pb-2" style="top: 70%;" v-show="currentType == 0">
