								<view class="theme-bg d-flex j-center a-center flex-column pt-2" style="width: 30%;">
									<view class="mb-2 height-100 d-flex a-center j-center width-000"  @tap="optCity(item,index)" :class="citySelectCurrent == index?'citySelectCurrent':'citySelectCurrentNo'" v-for="(item,index) in citySelect" :key="index">
										{{item.name}}
									</view>
								</view>
								<view class="d-flex flex-wrap" style="width: 70%;background: #803667;">
									<view @tap="optCity1(item,index)" class="mx-2 mt-3 d-flex a-center j-center" :class="item.activate?'activate':'activateNo'" v-for="(item,index) in citySelect[citySelectCurrent].chid" :key="index">
										{{item.name}}
									</view>
								</view>
							</view>
						</view>
						
						
					
									
					</view>
					
					
				</view>
				<!-- 类型选择 -->
			
			
			
			
			
		<!-- 	<view class="" >
				<view class="theme-bg" style="">
					<typeSelect  
					titleStyle0="font-size: 37rpx;font-family: PingFang SC;font-weight: bold;color: #FFFFFF;" 
					type="0"
					></typeSelect>
				</view>
			</view> -->
		</view>
	
		<view class="position-relative" style="top: -50rpx;">
			<!-- 头部 -->
			<view class="header-cont height-000 " >
				<view class="d-flex j-center flex-column height-000">
					<image src="../../static/img/banner.png" class="width-000 height-000" mode=""></image>
				</view>
			</view>
			<!-- 头部 -->
			<view class="">
				<view>
					<view class="theme-bg d-flex a-center width-000 j-center a-center" style="height: 80rpx;">
						<typeSelect  
						titleStyle0="font-size: 32rpx;font-family: PingFang SC;font-weight: 400;color: #FFFFFF;justify-content: center;height: 80rpx;" 
						titleStyle1="justify-content: center;height: 80rpx;"
						style="width: 100%;" 
						type="1"
						:isService="true"></typeSelect>
					</view>
				</view>
			</view>
			<!-- 时间选择 -->
			<view class="d-flex a-center height-100" style="overflow: auto;">
				<view @tap="cutDate(item,index)" class="width-100 px-3 d-flex a-center j-center flex-column" :class="dateList.current == index ?'current':'currentNo'" v-for="(item,index) in dateList.list" :key="index">
					<view class="">
						{{item.date}}
					</view>
					<view class="">
						{{item.text}}
					</view>
				</view>
			</view>
			<!-- 时间选择 -->
			
			<!-- 预约列表 -->
			<view class="">
				<commonListFill></commonListFill>
			</view>
			<view class="">
				<commonListFill></commonListFill>
			</view>
			<view class="">
