		</view>
	</view>
</template>

<script>
	import typeSelect from '@/components/common/type-select.vue'
	import commonListFill from '@/components/common/common-list-fill.vue'
	export default {
		components:{
			typeSelect,
			commonListFill
		},
		data(){
			return {
				currentType:-1,
				citySelectCurrent:0,
				citySelect:[
					{
						name:"武汉市",
						chid:[
							{
								name:'武汉市',
								activate:true
							},
							{
								name:'武汉市',
