							text:"今天"
						},
						{
							date:"10.16",
							text:"今天"
						},
						{
							date:"10.16",
							text:"今天"
						},
						{
							date:"10.16",
							text:"今天"
						},
						{
							date:"10.16",
							text:"今天"
						},
						{
							date:"10.16",
							text:"今天"
						},
						{
							date:"10.16",
							text:"今天"
						}
					]
				},
				navHeight:0,
				navHeightLine:0
			}
		},
		async mounted() {
			var menu = wx.getMenuButtonBoundingClientRect()
			var system = await wx.getSystemInfo()
			// console.log(menu,'胶囊')
			// console.log(system,'系统')
			this.navHeightLine = system.statusBarHeight + menu.height + (menu.top - system.statusBarHeight) * 2 + 30
			// this.navHeightLine = this.navHeight * 1.5
			console.log(system.statusBarHeight, menu.height)
			// console.log(this.navHeight)
		},
		methods:{
			optCity1(item,index){
				this.citySelect.forEach(item=>{
					item.chid.forEach((item1,index)=>{
						item1.activate = false
					})
				})
				item.activate = !item.activate
				console.log(item,index)
			},
			optCity(item,index){
				this.citySelectCurrent = index
			},
			typeOpt(type){
				if(type == this.currentType){
					this.currentType = -1
					return
				}
				this.currentType = type
			},
			cutDate(item,index){
				this.dateList.current = index
			}
		}
	}
</script>

<style>
	page{
		background: #803667;
	}
	.citySelectCurrent{
		
		font-size: 32rpx;
		font-family: PingFang SC;
		font-weight: 400;
		color: #FF2F68;
		
	}
	.citySelectCurrentNo{
		
